export default {
	onShow(){
		
		if (uni.getStorageSync('shopInfo')) {
			this.shopInfo = uni.getStorageSync('shopInfo')
			this.currentIndex = this.shopInfo.index
			this.weight = this.shopInfo.weight
			this.price = this.shopInfo.price
			if(this.shopInfo.status == 0) {
				this.choiceElese()
				this.elseShop = this.shopInfo.main
			} else {
				this.shopInfoMain = this.shopInfo.main
			}
		}
	},
	data () {
		return {
			shopList: [],
			currentIndex: 0,
			// 其他商品
			elseShop:'',
			// 重量
			weight: 1,
			//价值
			price: 1,
			//其他
			showElse: false,
			//选中的内容
			shopInfoMain: '',
			shopInfo: {}
		}
	},
	onLoad(){
		this.initGoodsInfo()
	},
	onUnload() {
		uni.hideKeyboard()
	},
	methods: {
		//更改下标
		handleCurrent (item,index) {
			// console.log(item)
			this.shopInfoMain = item
			this.currentIndex = index
			this.shopInfo.index = index
			this.showElse = false
		},
		//头部input
		handleElseShop () {
			this.currentIndex = -1
			this.showElse = true
		},
		//重量
		weightSub () {
			this.weight = Number(this.weight) - 1
			if (this.weight <= 0) {
				uni.showToast({
					icon: 'none',
					title: '重量不能再低啦'
				})
				this.weight = 1
			}
		},
		weightAdd () {
			this.weight = Number(this.weight) + 1
		},
		//价值
		priceSub () {
			this.price = Number(this.price) - 1
			if (this.price <= 0) {
				uni.showToast({
					icon: 'none',
					title: '价格不能再低啦'
				})
				this.price = 1
			}
		},
		priceAdd () {
			this.price = Number(this.price) + 1
			if (this.price > 500) {
				uni.showToast({
					icon: 'none',
					title: '价格不能再高啦'
				})
				this.price = 500
			}
		},
		checkWeight () {
			if (this.weight <= 0) {
				uni.showToast({
					icon: 'none',
					title: '重量不能再小啦'
				})
				this.weight = 1
			}
		},
		checkPrice () {
			if (this.price <= 0) {
				uni.showToast({
					icon: 'none',
					title: '价格不能再低啦'
				})
				this.price = 1
			}
			if (this.price > 500) {
				uni.showToast({
					icon: 'none',
					title: '价格不能再高啦'
				})
				this.price = 500
			}
		},
		//确定
		goBack () {
			if (this.shopInfoMain === '' && this.elseShop.trim() === '') {
				uni.showToast({
					icon: 'none',
					title: '请确认您的商品类型'
				})
				return
			}
			
			if (this.price > 500) {
				uni.showToast({
					icon: 'none',
					title: '商品预估价最高500元'
				})
				this.price = 500
				return
			}

			
			//其他
		
			if (this.showElse) {
				// let shopInfo = {}
				if (this.elseShop == "") {
					uni.showToast({
						icon: 'none',
						title: '请填写其他商品类型'
					})
					return
				}
				
				this.shopInfo.main = this.elseShop
				this.shopInfo.weight = this.weight
				this.shopInfo.price = this.price
				this.shopInfo.status = 0
				uni.setStorageSync('shopInfo',this.shopInfo)
			} else {
				let shopInfo = {}
				this.shopInfo.main = this.shopInfoMain
				this.shopInfo.weight = this.weight
				this.shopInfo.price = this.price
				this.shopInfo.status = 1
				uni.setStorageSync('shopInfo',this.shopInfo)
			}
			
			uni.navigateBack({
				delta:1
			})
		},
		//商品列表
		async initGoodsInfo () {
			let res = await this.$fetch(this.$api.goodsType,{},"GET","Form")
			
			if (res.code == 0) {
				this.shopList = res.data
			} else {
				uni.showToast({
					icon:'none',
					title: res.msg
				})
			}
			
		},
		//点击其他
		choiceElese () {
			this.showElse = true
			this.currentIndex = -1
		}
	},
	watch:{
		// weight (value) {
		// 	if (value === 0) {
		// 		uni.showToast({
		// 			icon: 'none',
		// 			title: '重量不能再小啦'
